'use client';

import { useRouter, usePathname } from 'next/navigation';
import { useSession, signOut } from 'next-auth/react';
import { useEffect, useState } from 'react';
import Link from 'next/link';
import { HomeIcon, UsersIcon, ChartBarIcon, Cog6ToothIcon, ChatBubbleLeftIcon } from '@heroicons/react/24/outline';

type NavItem = {
  name: string;
  href: string;
  icon: React.ComponentType<React.SVGProps<SVGSVGElement>>;
  current: boolean;
};

export default function AdminLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  const { data: session, status } = useSession();
  const router = useRouter();
  const pathname = usePathname();
  const [isLoading, setIsLoading] = useState(true);
  const [sidebarOpen, setSidebarOpen] = useState(false);

  const navigation: NavItem[] = [
    { name: 'Dashboard', href: '/admin', icon: HomeIcon, current: pathname === '/admin' },
    { name: 'Employees', href: '/admin/employees', icon: UsersIcon, current: pathname === '/admin/employees' },
    { name: 'Products', href: '/admin/products', icon: ChartBarIcon, current: pathname === '/admin/products' },
    { name: 'Reviews', href: '/admin/reviews', icon: ChatBubbleLeftIcon, current: pathname === '/admin/reviews' },
  ];

  useEffect(() => {
    if (status === 'loading') return; 
    if (status === 'unauthenticated') {
      router.push('/login');
    } else {
      setIsLoading(false);
    }
  }, [status, router]);

  const handleLogout = async () => {
    await signOut({ redirect: true, callbackUrl: '/login' });
  };

  if (isLoading || status === 'loading') {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-[#FF6B00] to-[#D80F2B]">
        <div className="flex flex-col items-center">
          <div className="w-16 h-16 border-4 border-white/20 border-t-white rounded-full animate-spin"></div>
          <p className="mt-4 text-white font-medium">Loading dashboard...</p>
        </div>
      </div>
    );
  }

  if (status === 'unauthenticated') {
    return null; 
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-orange-50 via-pink-50 to-white flex">
      {/* Sidebar for desktop */}
      <aside className="hidden lg:flex flex-col w-72 bg-gradient-to-b from-[#FF6B00] to-[#FF69B4] rounded-r-3xl shadow-xl p-6 text-white">
        <div className="flex items-center mb-10">
          <Link href="/admin" className="flex items-center">
            <img src="/next.svg" alt="Dunkin' Logo" className="h-10 w-10 mr-3 rounded-full bg-white p-1 shadow" />
            <span className="text-3xl font-extrabold tracking-tight">Dunkin&apos;</span>
          </Link>
        </div>
        <nav className="flex-1 space-y-2">
          {navigation.map((item) => (
            <Link
              key={item.name}
              href={item.href}
              className={`flex items-center px-4 py-3 rounded-xl transition-all duration-200 font-semibold text-lg ${item.current ? 'bg-white/90 text-[#FF6B00] shadow' : 'hover:bg-white/20 hover:text-white text-white/80'}`}
            >
              <item.icon className={`h-6 w-6 mr-3 ${item.current ? 'text-[#FF6B00]' : 'text-white/80'}`} />
              {item.name}
            </Link>
          ))}
        </nav>
        <div className="mt-10 flex items-center gap-3 p-4 bg-white/80 rounded-xl shadow">
          <div className="h-12 w-12 rounded-full bg-[#FF6B00] flex items-center justify-center text-white font-bold text-xl">
            {session?.user?.name?.charAt(0) || 'A'}
          </div>
          <div className="flex-1">
            <p className="font-bold text-[#FF6B00]">{session?.user?.name || 'Admin'}</p>
            <button
              onClick={handleLogout}
              className="text-xs font-semibold text-pink-600 hover:text-pink-800 mt-1"
            >
              Sign out
            </button>
          </div>
        </div>
      </aside>
      {/* Main content */}
      <div className="flex-1 flex flex-col">
        {/* Topbar for mobile */}
        <div className="lg:hidden flex items-center justify-between bg-gradient-to-r from-[#FF6B00] to-[#FF69B4] px-4 py-3 shadow">
          <div className="flex items-center">
            <Link href="/admin" className="flex items-center">
              <img src="/next.svg" alt="Dunkin' Logo" className="h-8 w-8 mr-2 rounded-full bg-white p-1 shadow" />
              <span className="text-xl font-extrabold tracking-tight text-white">Dunkin&apos;</span>
            </Link>
          </div>
          <button
            onClick={() => setSidebarOpen(true)}
            className="text-white hover:text-pink-100 focus:outline-none"
          >
            <svg className="h-7 w-7" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
            </svg>
          </button>
        </div>
        {/* Main content area */}
        <main className="flex-1 overflow-y-auto focus:outline-none p-8">
          <div className="max-w-7xl mx-auto">
            {children}
          </div>
        </main>
      </div>
      {/* Mobile sidebar overlay */}
      {sidebarOpen && (
        <div className="fixed inset-0 z-50 flex">
          <div className="fixed inset-0 bg-black/40" onClick={() => setSidebarOpen(false)}></div>
          <aside className="relative w-72 bg-gradient-to-b from-[#FF6B00] to-[#FF69B4] rounded-r-3xl shadow-xl p-6 text-white flex flex-col">
            <div className="flex items-center mb-10">
              <Link href="/admin" className="flex items-center">
                <img src="/next.svg" alt="Dunkin' Logo" className="h-10 w-10 mr-3 rounded-full bg-white p-1 shadow" />
                <span className="text-3xl font-extrabold tracking-tight">Dunkin&apos;</span>
              </Link>
            </div>
            <nav className="flex-1 space-y-2">
              {navigation.map((item) => (
                <Link
                  key={item.name}
                  href={item.href}
                  className={`flex items-center px-4 py-3 rounded-xl transition-all duration-200 font-semibold text-lg ${item.current ? 'bg-white/90 text-[#FF6B00] shadow' : 'hover:bg-white/20 hover:text-white text-white/80'}`}
                >
                  <item.icon className={`h-6 w-6 mr-3 ${item.current ? 'text-[#FF6B00]' : 'text-white/80'}`} />
                  {item.name}
                </Link>
              ))}
            </nav>
            <div className="mt-10 flex items-center gap-3 p-4 bg-white/80 rounded-xl shadow">
              <div className="h-12 w-12 rounded-full bg-[#FF6B00] flex items-center justify-center text-white font-bold text-xl">
                {session?.user?.name?.charAt(0) || 'A'}
              </div>
              <div className="flex-1">
                <p className="font-bold text-[#FF6B00]">{session?.user?.name || 'Admin'}</p>
                <button
                  onClick={handleLogout}
                  className="text-xs font-semibold text-pink-600 hover:text-pink-800 mt-1"
                >
                  Sign out
                </button>
              </div>
            </div>
          </aside>
        </div>
      )}
    </div>
  );
}
